.container__question {
    background-image: radial-gradient(
      circle at 37.58% 41.31%,
      #928f86 0,
      #4d515c 50%,
      #051b35 100%
    );
  
    @media screen and(max-width :420px) {
      margin: 0 auto;
      height: 100%;
      min-height: 100vh;
    }
  
    @media screen and(min-width :421px) {
      width: 100%;
      margin: 0 auto;
      height: 100%;
      min-height: 100vh;
    }
  
    @media screen and(min-width :800px) {
      width: 100%;
      padding: 0;
    }
  }



